import { Component, Injector, OnInit } from '@angular/core';
import { ActiveModalRef } from '@bmc-ux/adapt-angular';
import { Observable } from 'rxjs';
import {
  IApiPath,
  IEnvironmentData,
  IIpaasApiDefinition,
  IOrganizationData
} from '../ipaas-base-apis/ipaas-base-apis.types';
import { ApiEditorBase } from '../ipaas-base-apis/api-editor/api-editor-base.class';
import { RxJitterbitApisService } from './jitterbit-apis.service';
import { map } from 'rxjs/operators';
import { map as _map } from 'lodash';

@Component({
  selector: 'rx-jitterbit-api-editor',
  templateUrl: '../ipaas-base-apis/api-editor/api-editor.component.html',
  styleUrls: ['../ipaas-base-apis/api-editor/api-editor.component.scss']
})
export class JitterbitApiEditorComponent extends ApiEditorBase implements OnInit {
  constructor(
    private rxJitterbitApisService: RxJitterbitApisService,
    protected activeModalRef: ActiveModalRef,
    protected injector: Injector
  ) {
    super(activeModalRef, injector);
  }

  ngOnInit(): void {
    super.ngOnInit();
  }

  protected getOrganizationsAndEnvironments(): Observable<IOrganizationData[]> {
    return this.rxJitterbitApisService.getOrganizationsAndEnvironments().pipe(
      map((organizationEnvironmentList) =>
        _map(organizationEnvironmentList, (environments, organization) => ({
          name: organization,
          environments: _map(environments, (environment) => ({ name: environment }))
        }))
      )
    );
  }

  protected getApiPathDefinitions(environment: IEnvironmentData): Observable<IApiPath> {
    return this.rxJitterbitApisService.getApiPathDefinitions(this.selectedOrganization.name, environment.name);
  }

  protected getApiDefinition(apiId: string): Observable<IIpaasApiDefinition> {
    return this.rxJitterbitApisService.getApiDefinition(apiId);
  }

  protected editApiDefinition(apiDefinition: IIpaasApiDefinition): Observable<any> {
    return this.rxJitterbitApisService.editApiDefinition(apiDefinition);
  }

  protected createApiDefinition(apiDefinition: IIpaasApiDefinition): Observable<any> {
    return this.rxJitterbitApisService.createApiDefinition(apiDefinition);
  }

  protected getEnvironmentList(): IEnvironmentData[] {
    return this.organizations.find((value) => value.name === this.selectedOrganization.name)?.environments || [];
  }
}
